.registerErrorForm {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 380px;
  height: 452px;
  z-index: 100;
  background-color: #ffffff;

  &__container {
    margin: 22px 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    &-svg {
      width: 193px;
      height: 193px;
    }

    &-title {
      text-align: center;
      color: #333;
      font-family: "Montserrat";
      font-size: 21px;
      font-style: normal;
      font-weight: 900;
      line-height: normal;
      width: 100%;
      margin: 16px 0 24px;

      .highlight {
        display: none;
        font-weight: 400;
      }
    }

    &-Subtitle {
      color: #828282;
      font-family: "Rubik";
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      text-align: center;
      margin: 0 0 41px 0;
    }

    &-submit {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #000;
      font-family: "Montserrat";
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      text-transform: uppercase;
      margin-top: 0;
      width: 100%;
      background-color: #ffd540;
      border: none;
      border-radius: 30px;
      cursor: pointer;

      .icon {
        display: none;
      }
    }
  }

  @media (min-width: 768px) {
    width: 469px;
    height: 490px;

    &__container {
      margin: 57px 40px;

      &-svg {
        width: 171px;
        height: 171px;
      }

      &-title {
        font-size: 24px;
      }

      &-Subtitle {
        font-size: 16px;
      }
    }
  }

  @media (min-width: 1024px) {
    &__container {
      &-svg {
        width: 160px;
        height: 160px;
      }

      &-title {
        font-size: 28px;
      }

      &-Subtitle {
        font-size: 18px;
      }
    }
  }
}
